import React, { Component } from "react";
import "./Educations.css";
import { Fade } from "react-reveal";
import { useState, useEffect } from "react";

class Educations extends Component {
  render() {
    const theme = this.props.theme;

    return (
      <div className="main" id="educations">
        <div className="educations-header-div">
          <BrandExperience1 theme={theme} />
          <BrandExperience2 theme={theme} />
          <BrandExperience3 theme={theme} />
        </div>
      </div>
    );
  }
}

function BrandExperience1({ theme }) {
  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  const brandImages = [
    require(`../../assests/images/11.jpeg`),
    require(`../../assests/images/12.jpeg`),
    require(`../../assests/images/10.jpeg`),
    require(`../../assests/images/s4.jpeg`),

    // Add more brand image paths here if needed
  ];

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentImageIndex((prevIndex) => (prevIndex + 1) % brandImages.length);
    }, 2000); // Change image every 2 seconds

    return () => clearInterval(interval);
  }, [brandImages.length]);

  return (
    <Fade bottom duration={2000} distance="100px">
      <div className="experience-heading-div">
        <div className="experience-heading-text-div">
          <h1 className="experience-heading-text" style={{ color: theme.text }}>
            Raw Product Intake
          </h1>
          {/* <h3 className="experience-heading-sub-text" style={{ color: theme.text }}>
            Explore our wheat-based offerings
          </h3> */}
        </div>
        <div className="experience-heading-img-div">
          <img src={brandImages[currentImageIndex]} alt="" />
        </div>
      </div>
    </Fade>
  );
}

