import React, { Component } from "react";
import Header from "../../components/header/Header";
import Footer from "../../components/footer/Footer";
import TopButton from "../../components/topButton/TopButton";
import "./Experience.css";
import { experience } from "../../portfolio.js";
import { Fade } from "react-reveal";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import { useState, useEffect } from "react";

class Experience extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div className="education-main">
        <Header theme={theme} />
        <div className="basic-experience">
          <Fade bottom duration={2000} distance="40px">
            <div className="experience-heading-div">
              <div className="experience-heading-img-div">
                <img src={require(`../../assests/images/8.webp`)} alt="" />
              </div>
              <div className="experience-heading-text-div">
                <h1
                  className="experience-heading-text"
                  style={{ color: theme.text }}
                >
                  {experience.title}
                </h1>
                <h3
                  className="experience-heading-sub-text"
                  style={{ color: theme.text }}
                >
                  {experience["subtitle"]}
                </h3>
                <p
                  className="experience-header-detail-text subTitle"
                  style={{ color: theme.secondaryText }}
                >
                  {experience["description"]}
                </p>
              </div>
            </div>
          </Fade>
          <BrandExperience theme={theme} />
        </div>
        <Footer theme={this.props.theme} onToggle={this.props.onToggle} />
        <TopButton theme={this.props.theme} />
      </div>
    );
  }
}

function BrandExperience({ theme }) {
  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  const brandImages = [
    require(`../../assests/images/3.jpg`),
    require(`../../assests/images/4.jpg`),
    // Add more brand image paths here if needed
  ];

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentImageIndex((prevIndex) => (prevIndex + 1) % brandImages.length);
    }, 2000); // Change image every 2 seconds

    return () => clearInterval(interval);
  }, [brandImages.length]);

  return (
    <Fade bottom duration={2000} distance="40px">
      <div className="experience-heading-div">
        <div className="experience-heading-img-div">
          <img src={brandImages[currentImageIndex]} alt="" />
        </div>
        <div className="experience-heading-text-div">
          <h1 className="experience-heading-text" style={{ color: theme.text }}>
            Brands
          </h1>
          <h3
            className="experience-heading-sub-text"
            style={{ color: theme.text }}
          >
            Explore our wheat-based offerings
          </h3>
        </div>
      </div>
    </Fade>
  );
}

export default Experience;
